
.card-container {
  flex-basis: 180px;
  max-width: 180px;
  height: 180px;
  margin-top: 12px;
  padding: 0;
  border: 3px solid #aaa;
  border-radius: 4px;
  cursor: pointer;
}

.card-overlay {
  position: absolute;
  width: 180px;
  height: 180px;
}
.game-container {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: space-evenly;
}
body {
  font-family: 'Courier New', Courier, monospace;
}

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.game-menu {
  text-align: center;
}

.btn {
  outline: none;
  padding: 12px;
  margin: 2px auto;
  border-radius: 5px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
}

.btn-start {
  background-color: aquamarine;
  color: #008080;
  -webkit-transition: all .4s;
  transition: all .4s;
}

.btn-start:hover {
  background-color: #008080;
  color: aquamarine;
  border-radius: 8px;
}
